@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Lora:ital,wght@1,400;1,500;1,600;1,700&family=Poppins:wght@400;600&family=Varela&display=swap');

:root {
    --mainColor: #fff;
    --mainLightColor : rgba(235,235,235,0.5);
    --mainDimColor : rgb(228,228,228)
}
body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    box-sizing: border-box;
    
}
.containermusic {
    min-height: 100vh;
    min-width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--mainColor);
    font-family: "Poppins";
 background-color: #fff;
    
}
.blackScreen {
    width: 100vw;
    height: 100vh;
    position: absolute;
    pointer-events: none;
    
}
.music-container {
   
    width: 350px;
    padding: 35px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 36px;
    box-shadow: 0 0 20px rgba(26,26,26,0.1),
    0 0 40px rgba(26,26,26,0.1),
    0 0 80px rgba(26,26,26,0.1);
    font-weight: 600;
    backdrop-filter : blur(15px);
    background-color:#071A26;
}
.musicPlayer {
    margin: 0;
    margin-bottom: 10px;
    color: var(--mainLightColor);
}
.music-Head-Name {
    margin: 0 auto;
    font-size: 1.5rem;
    text-align: center;
}
.music-Artist-Name{
    color: var(--mainDimColor);
    margin: 4px 0;
    font-size: 1.2rem;
    font-weight: 400;

}
#songAvatar {
    width: 200px;
    height: 200px;
    border-radius: 50%;
 
    margin: 18px 0;
    position: relative;
    cursor: pointer;
    animation: animateAvatar .5s linear infinite;
}
@keyframes animateAvatar {
    0%,100% {
        transform: scale(1);
    }
    50%{
        transform: scale(1.03);
    }
}
.musicTimerDiv{
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-weight: 600;
}
.musicProgressBar{
    width: 100%;
    margin-bottom: 16px;
    height: 10px;
    border-radius: 5px;
    outline: none;
    filter: hue-rotate(20deg);
}
.playBtn {
    font-size: 32px;
    margin: 8px 10px;
    color: var(--mainColor);
    cursor: pointer;
    font-weight: 400;
}
.musicControlers{
    cursor: pointer;
    font-size: 28px;
    color: var(--mainColor);
}


.objectFitCover{
    object-fit: cover;
}
.objectFitContain{
    object-fit: contain;
}